@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap);
body {
  margin: 0;
  padding: 0;
  background:  no-repeat center center fixed;
  background-size: cover;
}
.App {
  display: grid;
  grid-template-columns: 10% 10% 10% 10% 10% 10% 10% 10% 10% 10%;

}
header {
  grid-column-start: 3;
  grid-column-end: 9;
  text-align: center;
  display: grid;
  grid-template-columns: 25% 50% 25%;
}
header h1 {
  font-family: 'Shadows Into Light', cursive;
  color:rgb(87, 178, 190);
  text-shadow: 1px 1px 1px black;
  grid-column-start: 1;
  grid-column-end: 4;
  
}

#city {
 border: 2px solid rgb(113, 216, 241);
 width:100%;
 min-width:150px;
 height:30px;
 text-align:center;
 font-size:18px;
 grid-column-start: 2;
 grid-column-end: 3;
 
}
#searchButton {
 border: 0px;
 height:39px;
 width:50%;
 min-width:100px;
 margin-left:auto;
 margin-right:auto;
 margin-top:0px;
 background-color: rgb(144, 218, 236);
 color:white;
 font-weight: 600;
 cursor:pointer;
 grid-column-start: 2;
 grid-column-end: 3;
}
#searchButton:hover {
  background-color: rgb(63, 169, 184);
}

.box {
  margin-top:20px;
  min-width:400px;
