.today {
  min-width:80px;
  min-height:90px;
  width:100%;
  margin-left:4%;
  margin-top:3%;
  display: grid;
  text-align: center;
  border-radius: 3px;
  color:white;
  font-weight: 700;
  text-shadow: 1px 1px 1px black;
  cursor: pointer;
  grid-column-start: 1;
  grid-column-end: 2;

opacity: 0.8;
}
.dayWeather:hover {
    opacity: 1;
}
.today img{
  margin-left:auto;
  margin-right: auto;
}
.todaySection {
  display: grid;
  grid-template-columns: 20% 25% 35% 20%;
  width:100%;
}
.todaySection ul {
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  -webkit-padding-start: 20px;
          padding-inline-start: 20px;

  list-style-type: none;
  grid-column-start: 2;
  grid-column-end: 3;
}
.todaySection li {
  margin:5px;
  color:white;
  font-weight: 700;
  text-shadow: 1px 1px 1px black;
}

.cityDisplayed {
  font-size:25px;
  color:white;
  font-weight: 700;
  text-shadow: 1px 1px 1px black;
  justify-content: center;
  justify-self: center;
  margin: 40px 10% 0 10%;
  grid-column-start: 3;
  grid-column-end: 4;
}
.buttons {
  grid-column-start: 4;
  grid-column-end: 5;
}
.buttons p {
  color:white;
  text-shadow: 0.5px 0.5px 0.5px black;
}
.settings  {
  width: 15%;
  height:20%;
  font-size:20px;
  background: none;
  border:none;
  cursor:pointer;
  opacity:0.7;
  float: right;
  margin-right:10px;
}
.refresh {
  width: 15%;
  height:20%;
  font-size:20px;
  background: none;
  border:none;
  cursor:pointer;
  opacity:0.7;
  float: right;
  margin-right:10px;
}
button:hover {
  opacity: 1;
}
.lol {
  font-size:400px;
}


.boxSettings {
  min-width:100vw;
  min-height:100vh;
  position: absolute;
  display:inline-block;
  grid-column-start: 2;
  grid-column-end: 10;
  border-radius:5px;
  background:rgb(78, 77, 77);
  opacity: 0.98;
  text-align: center;
  font-weight:600
}
.Settings ul {
  list-style-type: none;
}
.Settings ul li {
  margin:20px;
  font-size:18px;
 
}
.Settings  p {
    -webkit-margin-before: 0em;
            margin-block-start: 0em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
}

.switch {
  position: relative;
  display: inline-block;
  width:80px;
  height:34px;
}
.switch input {
  opacity: 0;
  width:0;
  height:0;
}
.slider {
  position: absolute;
  cursor: pointer;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background-color: #ccc;
  transition: .4s;
  border-radius:34px;
}
.slider:before {
  position: absolute;
  content: "";
  height:30px;
  width:26px;
  left:4px;
  top:2px;
  bottom:4px;
  background-color: white;
  transition: .4s;
  border-radius:50%;
}
input:checked + .slider {
  background-color: #63B1FF;
}
input:focus + .slider {
  box-shadow: 0 0 1px #63B1FF;
}
input:checked + .slider:before {
  -webkit-transform: translateX(46px);
  transform: translateX(46px)
}
.exit {
  border-radius:5px;
  font-size:20px;
  float: right;
  position: relative;
  top:-20px;
  cursor:pointer;
}

.alert {
  color:rgb(245, 40, 40);
  text-shadow:0.3px 0.3px 0.3px black;
  grid-column-start: 1;
  grid-column-end: 4;

}


